* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #d0eafd;
  min-height: 100vh;
}

.container {
  max-width: 700px;
  width: 100%;
  background: #ffffff;
  border-radius: 10px;
  padding: 35px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  margin: 0px 150px 0px 0px;
}
.translator-heading {
  font-size: 24px;
  font-weight: bold;
  color: #212121;
  text-align: center;
  margin-bottom: 15px;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2);
}

.wrapper {
  border: 1px solid #3c3c3c;
  border-radius: 10px;
}

.text-input {
  display: flex;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  border-bottom: 1px solid #3c3c3c;
  background-color: #f5f5f5;
}

.from-text-container {
  position: relative;
  width: 50%;
  border-right: 1px solid #bebaba;
}

.from-text {
  height: 250px;
  border: none;
  outline: none;
  border-radius: 10px 0 0 0;
  width: 100%;
  resize: none;
  padding: 10px;
  font-size: 18px;
  background-color: #f5f5f5;
}

.to-text {
  height: 250px;
  border: none;
  outline: none;
  border-radius: 0 10px 0 0;
  width: 50%;
  resize: none;
  padding: 10px;
  font-size: 18px;
  background-color: #f5f5f5;
}

.clear-icon {
  position: absolute;
  bottom: 15px;
  right: 15px;
  cursor: pointer;
  color: #888;
  font-size: 20px;
  transition: color 0.3s ease;
}

.clear-icon:hover {
  color: #333;
}

.controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  border-top: 1px solid #ddd;
}

.controls li {
  display: flex;
  align-items: center;
  list-style: none;
}

.icons {
  display: flex;
  gap: 10px;
}

.from-vol,
.from-copy,
.to-vol,
.to-copy,
.i-change {
  cursor: pointer;
  color: #888;
  font-size: 18px;
  transition: color 0.3s ease;
}

.from-vol:hover,
.from-copy:hover,
.to-vol:hover,
.to-copy:hover,
.i-change:hover {
  color: #000000;
}

select {
  border: none;
  border-radius: 5px;
  outline: none;
  font-size: 14px;
  cursor: pointer;
  margin: 0 10px;
  padding: 5px;
  background: transparent;
  transition: all 0.3s ease;
}

select:hover {
  background-color: #f0f0f0;
}

select:focus {
  outline: none;
  border-color: #1565c0;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

option {
  font-size: 14px;
  padding: 10px;
  background-color: #fff;
  color: #000;
}

button {
  width: 100%;
  padding: 10px;
  border: none;
  outline: none;
  color: #ffffff;
  background-color: #1976d2;
  cursor: pointer;
  margin-top: 20px;
  font-size: 18px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #1565c0;
}

button:disabled {
  background-color: #b0bec5;
  color: #757575;
  cursor: not-allowed;
  opacity: 0.7;
}

.error-message {
  color: red;
  margin-top: 10px;
  text-align: center;
  font-size: 14px;
}

/* Responsive Design */
@media (max-width: 700px) {
  .text-input {
    flex-direction: column;
  }
  .from-text-container {
    border: none;
  }
  .from-text-container,
  .to-text {
    width: 100%;
  }

  .from-text {
    border-right: none;
    border-bottom: 1px solid #ddd;
    border-radius: 10px 10px 0 0;
  }

  .to-text {
    border-radius: 0 0 10px 10px;
  }

  .controls {
    flex-direction: column;
    gap: 10px;
  }

  .controls li {
    width: 100%;
    justify-content: space-between;
  }
  li.change {
    align-items: center;
    justify-content: center;
  }
}
